/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: right;
}

.boton-amarillo-block {
  background-color: #ff9500;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border-radius: 1rem;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #e68600;
  cursor: pointer;
}

.boton-amarillo {
  background-color: #ff9500;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border-radius: 1rem;
  border: none;
}
.boton-amarillo:hover {
  background-color: #e68600;
  cursor: pointer;
}

.boton-verde {
  background-color: #4cd964;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border-radius: 1rem;
  border: none;
}
.boton-verde:hover {
  background-color: #37d552;
  cursor: pointer;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode h5, .dark-mode h6 {
  color: #e1e1e1;
}
.dark-mode .anuncio {
  background-color: #333333;
  color: #FFFFFF;
}
.dark-mode .anuncio .icono {
  filter: invert(1);
}
.dark-mode .precio {
  color: #4cd964;
}

/* UI INDEX */
.no-webp .inicio {
  background-image: url("../img/header.jpg");
}

.webp .inicio {
  background-image: url("../img/header.webp");
}

.header {
  background-color: #333333;
  background-size: cover;
}
.header.inicio {
  background-position: center center;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}
.header .contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header .contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  max-width: 50rem;
  line-height: 1.5;
}

.footer {
  padding: 2.5rem 0;
  background-color: #333333;
}
.footer .contenido-footer {
  padding-top: 2.5rem;
}
.footer a {
  color: #FFFFFF;
  text-decoration: none;
  padding: 0.5rem;
}
.footer a:hover {
  color: #4cd964;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
  font-size: 1.7rem;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
  padding-bottom: 3rem;
}
@media (min-width: 768px) {
  .barra {
    align-items: flex-end;
    flex-direction: row;
    justify-content: space-between;
  }
}

.derecha {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 3rem;
  filter: invert(1);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
    margin-bottom: 0;
  }
}

.navegacion {
  display: none;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  padding: 0.5rem;
  font-size: 1.6rem;
  display: block;
  text-align: center;
}
.navegacion a:hover {
  color: #4cd964;
}
@media (min-width: 768px) {
  .navegacion a {
    padding: 1rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0rem;
  }
}

/* Menu */
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 4rem;
  cursor: pointer;
}

.mostrar {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .mostrar {
    flex-direction: row;
  }
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  margin-top: 1.5rem;
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  border-radius: 1.5rem;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
  flex: 1;
}
.anuncio h3 {
  line-height: 1.4;
  margin: 0 0 1rem 0;
  font-weight: 700;
  font-size: 2rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.anuncio p {
  font-weight: 100;
}
.anuncio img {
  border-top-left-radius: 1.5rem;
  border-top-right-radius: 1.5rem;
  width: 100%;
  height: auto;
}

.precio {
  color: #4cd964;
  font-weight: 700;
  font-size: 2.5rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0rem;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.no-webp .imagen-contacto {
  background-image: url("../img/encuentra.jpg");
}

.webp .imagen-contacto {
  background-image: url("../img/encuentra.webp");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #FFFFFF;
  margin: 0 2rem 0 2rem;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-left: 2rem;
  margin-right: 2rem;
  margin-bottom: 2rem;
}
.entrada-blog img {
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
    margin-left: 2rem;
  }
  .entrada-blog:last-of-type {
    margin-bottom: 0;
  }
}

.informacion-meta span {
  color: #ff9500;
}

.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  width: 15rem;
  height: 0.5rem;
  display: block;
  background-color: #4cd964;
  margin-top: 1rem;
}
.texto-entrada span {
  color: #ff9500;
}

.testimonial {
  background-color: #2ac845;
  font-size: 2.4rem;
  padding: 2rem;
  line-height: 3rem;
  border-radius: 1rem;
  color: #FFFFFF;
  margin: 0 2rem 2rem 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .testimonial {
    margin-right: 2rem;
  }
}

/* UI NOSOTROS */
.sobre-nosotros {
  margin-left: 2rem;
  margin-right: 2rem;
}
@media (min-width: 768px) {
  .sobre-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.sobre-nosotros img {
  border-radius: 1rem;
}
.sobre-nosotros h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
  font-weight: 700;
}

/* UI Formulario */
.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea, .formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 10rem;
}
.formulario .forma-contacto {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 50%;
}