.no-webp .imagen-contacto {
    background-image: url('../img/encuentra.jpg'); // Verifica que esta ruta sea correcta
}

.webp .imagen-contacto {
    background-image: url('../img/encuentra.webp'); // Verifica que esta ruta sea correcta
}

.imagen-contacto {
    background-position: center center;
    background-size: cover;
    padding: $separacion * 2 0;
    display: flex;
    flex-direction: column;
    align-items: center;

    h2 {
        font-size: 4rem;
        font-weight:  $black;
        color: $blanco;
    }

    p {
        font-size: 1.8rem;
        color: $blanco;
        margin: 0 2rem 0 2rem;
    }
}